import { HttpClient, JsonpInterceptor } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Mobile, MobileGrouped, Version } from '../models/mobile'

@Injectable({
  providedIn: 'root'
})
export class MobileApiService {
  private prefix: string = 'MobilePhone'

  constructor(private http: HttpClient) { }

  getRamOptions() {
    return this.http.get(`${environment.api}/MobileRam`).pipe(map(res => {
      return res['data']
    }))

  }

  getStorageOptions() {
    return this.http.get(`${environment.api}/MobileStorage`).pipe(map(res => {
      return res['data']
    }))
  }

  getSimCardOptions() {
    return this.http.get(`${environment.api}/MobileSimCard`).pipe(map(res => {
      return res['data']
    }))
  }

  getDeviceBrand() {
    return this.http.get(`${environment.api}/MobileBrand`).pipe(map(res => {
      return res['data']
    }))
  }

  getConnectivityOptions() {
    return this.http.get(`${environment.api}/MobileConnectivity`).pipe(map(res => {
      return res['data']
    }))
  }

  getColorOptions() {
    return this.http.get(`${environment.api}/MobileColor`).pipe(map(res => {
      return res['data']
    }))
  }


  getMobileModel() {
    return this.http.get(`${environment.api}/MobileModel`).pipe(map(res => {
      return res['data']
    }))
  }



  getAllMobilePhones() {
    return this.http.get(`${environment.api}/${this.prefix}`).pipe((map((mobiles: MobileGrouped) => {
      return mobiles;
    })))
  }

  mapObjectToString(value: object) {
    return JSON.stringify(value);
  }


  addMobile(mobile: Mobile) {

    (mobile['versions'] as any) = JSON.stringify(mobile['versions'])
    console.log(mobile)
    return this.http.post(`${environment.api}/${this.prefix}`, mobile).pipe(map(res => {
      return res
    }))
  }

  editMobile(mobileId: number, newData: Mobile) {
    return this.http.put(`${environment.api}/${this.prefix}/${mobileId}`, newData).pipe(map(res => {
      return res
    }))
  }

  deleteMobile(mobileId: number) {
    return this.http.delete(`${environment.api}/${this.prefix}/${mobileId}`).pipe(map(res => {
      return res
    }))
  }

  /**             Version              */

  addVersion(version: Version) {
    return this.http.post(`${environment.api}/MobilePhoneVersion`, version).pipe(map(res => {
      return res
    }))
  }

  updateVersion(version: Version) {
    return this.http.put(`${environment.api}/MobilePhoneVersion/${version.id}`, version).pipe(map(res => {
      return res
    }))
  }

  deleteversion(versionId: number) {
    return this.http.delete(`${environment.api}/MobilePhoneVersion/${versionId}`).pipe(map(res => {
      return res
    }))
  }



}
